@import "assets/global";

.elm-subscripition {

  form {
    .subscription-form-inner {
      position: relative;
      width: auto;
      width: 95%;
      max-width: 800px;
      margin: 0 auto;
      height: 50px;
      background: #f2f2f2;
      border-radius: 50px;
      border: 0px;
      outline: 0px;

      .subscription-input {
        display: inline-block;
        background: inherit;
        height: inherit;
        width: inherit;
        border: inherit;
        border-radius: inherit;
        text-align: left;
        position: absolute;
        left: 0;
        width: 100%;
        padding-left: 1rem;
        padding-right: 180px;

        &:focus {
          border: none;
          border-radius: inherit !important;
        }

        @include tinyMobile {
          & {
            padding-right: 1rem;
          }
        }

        @include mobile {
          & {
            padding-right: 1rem;
          }
        }
      }

      .subscription-button {
        position: absolute;
        top: -10px;
        right: -10px;
        background-color: transparent;
        background-image: url('../../assets/img/subscription_button.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 70px;
        width: 180px;
        font-size: 1.2em;
        font-weight: 400;
        padding-top: 0px;
        box-shadow: 0px;
        text-shadow: 0px;
        outline: 0px;
        border: 0px;
        z-index: 1000;
        cursor: pointer;

        @include tinyMobile {
          & {
            top: 60px;
            right: -10px;
          }
        }

        @include mobile {
          & {
            top: 60px;
            right: -10px;
          }
        }
      }
    }
  }
}