@import "assets/global";

.elm-blog {
  h3 {
    @include tinyMobile {
      text-align: center;
    }
    @include mobile {
      text-align: center;
    }
  }
  div[class*="col"] {
    align-self: stretch;

    .blog-item-container { 
      height: 100%;
        
      .image-container {
        position: relative; 
        
        .tag-container {
          position: absolute;
          bottom: 0;
          right: 0;
          left: 0;
          
          span {
            display: inline-block;
            border-radius: 5rem;
            background-color: orange;
            
            &.blue-tag {
              background-color: #374795;
            }
          }
        }
      }

      .content-container {
        background-color: #fafafa;
        max-height: 100%;
        height: calc(100% - 200px);
        position: relative;

        a[class*="more-link"] {
          position: absolute;
          bottom: 0;
          left: 0;
          
          i {
            vertical-align: middle;
          }
        }
      }
    }
  }
}

.justify-content-center {
  justify-content: center !important;
}

.justify-self-center {
  justify-self: center !important;
}