@import './assets/font';
@import './assets/global';

.app {
  font-family: $iransans;
  direction: rtl;
  text-align: justify;
}

.w-90 {
  width: 90% !important;
}

.justify-content-center {
  justify-content: center !important;
}

.header-picture {
  background-image: url('assets/img/hero_top_background.png');
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
  max-width: 100%;

  @include mobile {
    & {
      background-image: none;
    }
  }

  @include tinyMobile {
    & {
      background-image: none;
    }
  }

  @include tablet {
    & {
      background-image: none;
    }
  }
}

.footer-picture {
  background-image: url('assets/img/hero_bottom_background.png');
  background-repeat: no-repeat;
  background-position: bottom -5vh center;
  background-size: contain;
  max-width: 100%;

  @include largeScreen {
    & {
      background-position: bottom -15vh center;
    }
  }

  @include mobile {
    & {
      background-image: none;
    }
  }

  @include tinyMobile {
    & {
      background-image: none;
    }
  }

  @include tablet {
    & {
      background-image: none;
    }
  }
}

.text-dark-blue {
  color: #111b41;
}

.text-gray {
  color: #aaa;
}

.ltr {
  direction: ltr !important;
}

.font-weight-light {
  font-weight: 300 !important;
}

.dashed-underline-gray {
  border-bottom: 1px dashed #aaa;
}
