@import 'assets/global';

.elm-header {
  padding-bottom: 5rem;

  .title {
    margin-top: 7rem;
    margin-bottom: 10rem;
    margin-right: auto;
    margin-left: auto;

    @include mobile {
      & {
        margin-bottom: 3rem;
      }
    }
  
    @include tinyMobile {
      & {
        margin-bottom: 3rem;
      }
    }
  
    @include tablet {
      & {
        margin-bottom: 3rem;
      }
    }
  }

  .header-image {
    max-width: 80vw;
  }

  @include mobile {
    & {
      background-color: #233f70;
      margin: 0px;
      text-align: center;
    }
  }

  @include tinyMobile {
    & {
      background-color: #233f70;
      margin: 0px;
      text-align: center;
    }
  }

  @include tablet {
    & {
      background-color: #233f70;
      margin: 0px;
      text-align: center;
    }
  }
}