@import "assets/global";

.mt-10 {
  margin-top: 30vh !important;
}

.dashed-underline {
  border-bottom: 1px dashed #fff;
}

.footer {
  max-width: 1000px;
  justify-self: center;

  @include mobile {
    & {
      background-color: #233f70;
      margin: 0px;
      text-align: center;
    }
  }

  @include tinyMobile {
    & {
      background-color: #233f70;
      margin: 0px;
      text-align: center;
    }
  }

  @include tablet {
    & {
      background-color: #233f70;
      margin: 0px;
      text-align: center;
    }
  }
}